


















































import Vue from 'vue'
import { createOrUpdateMenu, getEditMenuInfo } from '@/services/menu'
import { Form } from 'element-ui'

export default Vue.extend({
  name: 'MenuCreateOrEdit',
  props: {
    isEdit: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      form: {
        parentId: -1, // -1 表示没有上级菜单
        name: '',
        href: '',
        icon: '',
        orderNum: 0,
        description: '',
        shown: false
      },
      rules: {
        name: [
          { required: true, message: '请输入名称', trigger: 'change' }
        ],
        href: [
          { required: true, message: '请输入菜单路径', trigger: 'change' }
        ],
        icon: [
          { required: true, message: '请输入图标', trigger: 'change' }
        ],
        description: [
          { required: true, message: '请输入描述', trigger: 'change' }
        ]
      },
      parentMenuList: [] // 父级菜单列表
    }
  },

  created () {
    this.loadMenuInfo()
  },

  methods: {
    async loadMenuInfo () {
      const { data } = await getEditMenuInfo(this.$route.params.id || -1)
      if (data.data.menuInfo) {
        this.form = data.data.menuInfo
      }
      if (data.code === '000000') {
        this.parentMenuList = data.data.parentMenuList
      }
    },

    async onSubmit () {
      // 1. 表单验证
      // 2. 验证通过，提交表单
      (this.$refs.form as Form).validate(async (valid) => {
        if (valid) {
          const { data } = await createOrUpdateMenu(this.form)
          if (data.code === '000000') {
            this.$message.success('提交成功')
            this.$router.back()
          }
        }
      }
      )
    }
  }
})
